<template>
  <div>
    <v-container>
     <v-layout justify-center>
    <v-flex xs12 sm8 v-for='(pro, i) in profile' :key='i'>
          <v-flex xs12>
            <h3 justify-text-center>Account Details</h3>
          </v-flex>
          <v-layout row wrap>
            <v-alert  v-model="alert" :type="colorValue" dismissible>
              {{message}}
            </v-alert>
              <v-flex xs12 mt-4>
                <v-text-field
                      @click="updateValue(pro.username, 'username')"
                      name="username"
                      label="Name"
                      id="id"
                      v-model="pro.username"
                      :rules="usernameRules"
                      :disabled="textField"
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                      @click="updateValue(pro.email, 'email')"
                      name="email"
                      label="Email"
                      id="id"
                      v-model="pro.email"
                      :rules="emailRules"
                      :disabled="textField"
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                      @click="updateValue(pro.mobile, 'mobile')"
                      name="phone"
                      label="Phone"
                      id="id"
                      v-model="pro.mobile"
                      :rules="phoneRules"
                      :disabled="textField"
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                      @click="updateValue(pro.address, 'address')"
                      name="address"
                      label="Address"
                      id="email"
                      v-model="pro.address"
                      :rules="addressRules"
                      :disabled="textField"
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-btn :disabled="!valid" @click="finalSave()" v-show="saveUserData == false">Confirm</v-btn>
                <v-btn :disabled="!valid" @click="cancelUserData" v-show="saveUserData == false">Cancel</v-btn>
                <v-btn  @click="onClickEdit()" v-show="saveUserData" block>Edit</v-btn>
              </v-flex>
            </v-layout>
            <v-flex>
              <v-btn @click="changePass()" block v-show="saveUserData">Change Password</v-btn>
            </v-flex>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-container>
  </div>
</template>
<script>
import navbar from '@/components/useful/NavBar.vue'
import localForage from 'localforage'
import axios from 'axios'
export default {
  components: {
    'nav-bar': navbar
  },
  mounted () {
    this.fethcLocalForageData()
    // localForage.setItem('user', label)
    // console.log(user)
  },
  data () {
    return {
      saveUserData: true,
      textField: true,
      updateUser: true,
      colorValue: 'success',
      message: '',
      valid: true,
      autofocus: true,
      persistent: true,
      username: '',
      email: '',
      mobile: '',
      address: '',
      profile: [],
      updateText: '',
      label: '',
      devise_id: '',
      token: '',
      userEmail: '',
      ifFailure: false,
      msg: '',
      isCurrenPassSucess: true,
      alert: false,
      emailRules: [
        v => !!v || 'Email is required',
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'Email must be valid'
      ],
      phoneRules: [
        v => !!v || 'Phone No is required',
        v => (v && v.length === 10) || 'Phone Number must be Valid'
      ],
      usernameRules: [
        v => !!v || 'Username is required'
      ],
      addressRules: [
        v => !!v || 'Address is required'
      ]
    }
  },
  methods: {
    cancelUserData () {
      this.textField = true
      this.saveUserData = true
      this.getData()
    },
    onClickEdit () {
      this.textField = false
      this.saveUserData = false
    },
    changePass () {
      this.$router.push('profile/changepass')
    },
    updateValue (value, label) {
      this.msg = ''
      this.updateText = value
      this.label = label
    },
    onSave () {
      this.finalSave()
    },
    finalSave () {
      var obj = this
      var pro = obj.profile[0]
      console.log(obj.token)
      console.log(obj.devise_id)
      console.log(obj.userEmail)
      axios({
        url: obj.$hostname + '/users/updateprofile_user',
        method: 'post',
        headers: {
          token: obj.token,
          deviseid: obj.devise_id,
          email: obj.userEmail
        },
        data: {
          'username': pro.username,
          'email': pro.email,
          'mobile': pro.mobile,
          'address': pro.address
        }
      }).then(response => {
        obj.alert = true
        console.log(response.data)
        console.log(response.data.message)
        console.log(response.data.success)
        if (response.data.success === true) {
          localForage.setItem('user', response.data).then(function () {
            obj.message = response.data.message
            obj.userEmail = response.data.email
            obj.alert = true
            obj.colorValue = 'success'
            obj.textField = true
            obj.saveUserData = true
            obj.fethcLocalForageData()
            obj.getData()
          }).catch(error => {
            obj.alert = true
            obj.colorValue = 'error'
            obj.message = 'Error in Storing Data'
            console.log(error)
          })
        } else {
          obj.alert = true
          obj.colorValue = 'error'
          obj.message = response.data.message
        }
      }).catch(error => {
        obj.alert = true
        obj.colorValue = 'error'
        obj.message = 'Internal Error'
        console.log(error)
      })
    },
    fethcLocalForageData () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        console.log(value)
        obj.devise_id = value.devise_id
        obj.token = value.token
        obj.userEmail = value.email
      }).catch(error => {
        console.log(error)
      })
    },
    getData () {
      var obj = this
      obj.profile = []
      localForage.getItem('user').then(function (value) {
        obj.profile.push(value)
        console.log(value)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>



