<template>
  <div>
    <please-wait :progress="progress"></please-wait>
    <v-container>

      <v-alert v-model="alert" dismissible type="success">
        Irrigation will start in 10 seconds.
      </v-alert>

      <div class="text-xs-right">
        <phase-indicator></phase-indicator>
      </div>
      <!-- <div class="mt-3">Field Info:</div> -->
      <v-expansion-panel class="mt-3">
        <v-expansion-panel-content
        v-for="(fieldcrop,i) in fieldcrops"
        :key="i"
        >
        <div slot="header">
          <b>{{fieldcrop.crop.cropName}}</b> |
          {{fieldcrop.field.fieldName}}  |
          <b>{{fieldcrop.irrigationMode === 1?'Automatic':fieldcrop.irrigationMode === 2?'Semi-Automatic':fieldcrop.irrigationMode === 3?'Manual':'Disable'}}</b>
        </div>
        <v-dialog v-model="dialog" width="500">
          <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>Shut Down</v-card-title>
            <v-card-text>
              Are you sure you want to turn <b>Off</b> all the pumps and valves on this field
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="error" flat @click="closeAll(fieldcrop), dialog = false">Yes</v-btn>
              <v-btn color="success" flat @click="clickNo">No</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-card class="grey lighten-3">
          <v-card-text>
            <v-layout row>
              Crop Name : &nbsp;<b>{{fieldcrop.crop.cropName}}</b>
            </v-layout>
            <v-layout row>
              Field Name : &nbsp;<b>{{fieldcrop.field.fieldName}}</b>
            </v-layout>
            <span v-if="fieldcrop.field.fieldactuator.length >= 2">
            <v-layout row>
              Pump :&nbsp;
              <span v-for="(actuator,i) in fieldcrop.field.fieldactuator":key="i">
                <span v-if="actuator.actuator.actuatorTypeId === 2"> {{actuator.actuator.actuatorName}}</span>
              </span>
            </v-layout>
            <v-layout row>
              Valve :&nbsp;
              <span v-for="(actuator,i) in fieldcrop.field.fieldactuator":key="i">
                <span v-if="actuator.actuator.actuatorTypeId === 1">
                  {{actuator.actuator.actuatorName}}
                  <span v-if="++i < fieldcrop.field.fieldactuator.length">,&nbsp;</span>
                </span>
              </span>
            </v-layout>
            <v-layout row v-show="fieldcrop.irrigationMode === 1?true:false">
              <v-flex xs4>
                Irrigation Schedule
              </v-flex>
              <v-flex xs8 class="text-xs-right">
                <v-btn
                  slot="activator"
                  :color="fieldcrop.irrigationSwitch === 0?
                  'success'
                  :fieldcrop.irrigationSwitch === 1?'error'
                  :fieldcrop.irrigationStatus === 0?'success'
                  :'error'"
                  small flat outline dark
                  @click="getDisable(fieldcrop,fieldcrop.irrigationStatus)"
                  >
                {{fieldcrop.irrigationSwitch === 0?
                  'Start'
                  :fieldcrop.irrigationSwitch === 1?'Stop'
                  :fieldcrop.irrigationStatus === 0?'Start'
                  :'Stop'}}
                </v-btn>
              </v-flex>
            </v-layout>
            <v-layout row v-show="fieldcrop.irrigationMode === 2?true:false">
              <v-flex xs4>
                Add Irrigation <br> Schedule
              </v-flex>
              <v-flex xs8 class="text-xs-right">
                <!-- Button for Turn Actuator -->
                <!-- <v-btn slot="activator" :color="fieldcrop.valveStatus === false?'success':'error'" small flat outline @click="getActuatorControl(fieldcrop,fieldcrop.valveStatus)" :disabled="fieldcrop.timer">{{fieldcrop.valveStatus === false?'Turn On':'Turn Off'}}</v-btn>
                <v-btn small flat outline color='success' @click="getValve(fieldcrop)" :disabled="fieldcrop.valveStatus === true?true:fieldcrop.timer === true?true:false">Timer</v-btn> -->
              <!-- <span v-if="fieldcrop.timerStartTime"> -->
              <span>
                 <!-- {{timerCountDown(fieldcrop.timerStartTime, fieldcrop.timerDuration)}} -->
                 <!-- <v-text-field v-model="showCountDown" v-show=true></v-text-field> -->
                 <span v-if="fieldcrop.timer">
                   <!-- {{fieldcrop.showCountDown}} -->
                   <v-btn color="error" @click="turnOffIrrigation(fieldcrop.timerData)" small flat outline dark >Turn Off</v-btn>
                 </span>
                 <span v-else>
                   <v-btn color="success" @click="getValve(fieldcrop)" small flat outline dark >Turn On</v-btn>
                 </span>
               </span>
               <!-- <v-dialog v-model="isOver" persistent max-width="290">
                  <v-card>
                    <v-card-title class="headline">Irrigation has been over</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="green darken-1" flat @click.native="isOver = false">close</v-btn>
                    </v-card-actions>
                  </v-card>
               </v-dialog> -->
              </v-flex>
            </v-layout>
            <v-layout row wrap class="text-xs-center" v-if="fieldcrop.timer">
              <v-flex>
                <vue-countdown-timer
                  @start_callback="startCallBack('event started')"
                  @end_callback="endCallBack('event ended')"
                  :start-time="fieldcrop.timerStartTime"
                  :end-time="fieldcrop.timerEndTime"
                  :interval="1000"
                  :start-label="'Turing On...'"
                  :end-label="'Irrigation Started'"
                  label-position="begin"
                  :end-text="'Irrigation Ended!'"
                  :hour-txt="'hr'"
                  :minutes-txt="'min'"
                  :seconds-txt="'sec'">
                  <template slot="start-label" slot-scope="scope">
                    <span style="color: red" v-if="scope.props.startLabel !== '' && scope.props.tips && scope.props.labelPosition === 'begin'">{{scope.props.startLabel}}:</span>
                    <span style="color: blue" v-if="scope.props.endLabel !== '' && !scope.props.tips && scope.props.labelPosition === 'begin'">{{scope.props.endLabel}}:</span>
                  </template>

                  <template slot="countdown" slot-scope="scope">
                    <span>{{scope.props.days}}</span><i>{{scope.props.dayTxt}}</i>
                    <span>{{scope.props.hours}}</span><i>{{scope.props.hourTxt}}</i>
                    <span>{{scope.props.minutes}}</span><i>{{scope.props.minutesTxt}}</i>
                    <span>{{scope.props.seconds}}</span><i>{{scope.props.secondsTxt}}</i>
                  </template>

                  <template slot="end-label" slot-scope="scope">
                    <span style="color: red" v-if="scope.props.startLabel !== '' && scope.props.tips && scope.props.labelPosition === 'end'">{{scope.props.startLabel}}:</span>
                    <span style="color: blue" v-if="scope.props.endLabel !== '' && !scope.props.tips && scope.props.labelPosition === 'end'">{{scope.props.endLabel}}:</span>
                  </template>

                  <template slot="end-text" slot-scope="scope">
                    <span style="color: green">{{ scope.props.endText}}</span>
                  </template>
                </vue-countdown-timer>
              </v-flex>
            </v-layout>
          </span>
          <span v-else>
            <v-layout>
              No&nbsp;<b>pump</b>&nbsp;/&nbsp;<b>value</b>&nbsp;connected to this field.
            </v-layout>
          </span>
          </v-card-text>
        </v-card>
      </v-expansion-panel-content>
    </v-expansion-panel>
    <v-dialog v-model="endTime" width="500">
      <v-card>
        <v-card-title class="headline grey lighten-2" primary-title>
          Set Time Interval for Field : {{fieldName}} & Crop : {{cropName}} irrigation
        </v-card-title>
        <v-card-text class="text-xs-center">
          <!-- <v-btn round color="primary" @click = "irrigation(10)" dark>10</v-btn>
          <v-btn round color="primary" @click = "irrigation(20)" dark>20</v-btn>
          <v-btn round color="primary" @click = "irrigation(30)" dark>30</v-btn>
          <v-btn round color="primary" @click = "irrigation(60)" dark>60</v-btn> -->
          <v-radio-group v-model="irrigationDuration" row @click="clearDuration('custom')">
              <v-radio
                v-for="(optionValue, i) in durationOption"
                :key="i"
                :label="optionValue.text"
                :value="optionValue.value"
              ></v-radio>
            </v-radio-group>
            <v-text-field
              v-model.number="customDuration"
              label="Custom"
              type="number"
              @click="clearDuration('radio')"
            ></v-text-field>
            <v-btn round color="primary" @click = "irrigation(irrigationDuration, customDuration)" dark>Confirm</v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog v-model="phaseInfoDailog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">3 phase not avaliable for the pump to start</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="phaseInfoDailog = false">close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</div>
</template>
<script>
import navbar from '@/components/useful/NavBar.vue'
import ECPIndicator from '@/components/useful/ElectricityPhaseIndicator'
import axios from 'axios'
import PleaseWait from '../useful/PleaseWait.vue'
import localForage from 'localforage'

var deviseid
var token
var email
export default {
  computed: {
    getDuration () {
      return `${this.days}d ${this.hours}h ${this.minutes}m ${this.seconds}s`
    }
  },
  name: 'Pump',
  data () {
    return {
      timerField: [],
      showCountDown: '',
      countDownDate: new Date(),
      days: 0,
      hours: 0,
      minutes: 0,
      seconds: 0,
      distance: 0,
      // isOver: false,
      progress: false,
      dialog: '',
      validated: '',
      val2: true,
      val1: false,
      labelMA: 'Automatic',
      label: 'off',
      fieldcrops: [],
      fcDisableInfo: [],
      endTime: false,
      phaseInfoDailog: false,
      phaseStatus: '',
      fc: '',
      fieldName: '',
      cropName: '',
      alert: false,
      msg: '',
      actuators: [],
      mcid: '',
      pumpId: '',
      irrigationDuration: '',
      durationOption: [{'id': 1, 'text': '10', 'value': 10}, {'id': 2, 'text': '20', 'value': 20}, {'id': 3, 'text': '30', 'value': 30}, {'id': 4, 'text': '60', 'value': 60}],
      customDuration: '',
      fcis: [],
      timex: null
    }
  },
  components: {
    'phase-indicator': ECPIndicator,
    'nav-bar': navbar,
    'please-wait': PleaseWait
  },
  methods: {
    startCallBack: function (x) {
      console.log(x)
    },
    endCallBack: function (x) {
      console.log(x)
      this.onPageLoad()
    },
    clearDuration (msg) {
      if (msg === 'custom') {
        this.customDuration = ''
      } else if (msg === 'radio') {
        this.irrigationDuration = ''
      }
    },
    turnOffIrrigation (fc) {
      var obj = this
      console.log(fc)
      axios({
        method: 'post',
        url: this.$hostname + '/fieldcropirrigations/turnoff_extra_irrigation',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'stopirrigation': fc
        }
      }).then(response => {
        console.log(response)
        clearInterval(obj.timex)
        obj.showCountDown = null
        obj.onPageLoad()
      }).catch(error => { console.log(error) })
    },
    getActuatorControl (fieldcrop, valveStatus) {
      console.log(fieldcrop)
      console.log(valveStatus)

      var obj = this
      obj.fc = fieldcrop
      obj.msg = 'direct actuation'
      if (valveStatus === true) {
        obj.dialog = true
      } else {
        obj.changeActuatorStatus(1)
      }
    },
    getDisable (fieldcrop, irrigationStatus) {
      var obj = this
      obj.fc = fieldcrop
      obj.msg = 'irrigation control'
      console.log(fieldcrop)
      console.log(irrigationStatus)
      if (irrigationStatus !== 0 && fieldcrop.irrigationSwitch !== 0) {
        obj.dialog = true
      } else {
        if (fieldcrop.irrigationSwitch === 1) {
          obj.changeIrrigationStatus(0)
        } else {
          obj.changeIrrigationStatus(1)
        }
      }
    },
    changeActuatorStatus (status) {
      console.log('CAS')
      console.log(status)
      console.log(this.fc.fieldCropId)
      this.actuators = []
      var i
      for (i = 0; i < this.fc.field.fieldactuator.length; i++) {
        this.actuators.push(this.fc.field.fieldactuator[i].actuator.actuatorId)
        if (this.fc.field.fieldactuator[i].actuator.actuatorTypeId === 2) {
          this.mcid = this.fc.field.fieldactuator[i].actuator.mcId
          this.pumpId = this.fc.field.fieldactuator[i].actuator.actuatorId
        }
      }
      console.log(this.actuators)
      console.log(this.mcid)
      axios({
        method: 'post',
        url: this.$hostname + '/fieldactuator/onoffactuator',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'fieldCropId': this.fc.fieldCropId,
          'valveStatus': status,
          'mcid': this.mcid,
          'actuators': this.actuators,
          'pumpId': this.pumpId
        }
      }).then(response => {
        console.log(response)
        this.onPageLoad()
      }).catch(error => { console.log(error) })
    },
    changeIrrigationStatus (status) {
      console.log('CIS')
      axios({
        method: 'post',
        url: this.$hostname + '/fieldcropirrigations/irrigationstatus',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'fieldCropId': this.fc.fieldCropId,
          'irrigationSwitch': status
        }
      }).then(response => {
        console.log(response)
        this.onPageLoad()
      }).catch(error => { console.log(error) })
    },
    getValve (fieldcrop) {
      console.log(fieldcrop)
      var obj = this
      this.endTime = true
      obj.fc = fieldcrop
      obj.fieldName = obj.fc.field.fieldName
      obj.cropName = obj.fc.crop.cropName
      // var j = obj.fieldcrops.indexOf(fieldcrop)
      // obj.timer2(fieldcrop.timerDuration, fieldcrop.timerStartTime, j)
      history.pushState(null, null, location.href)
      window.onpopstate = function () {
        history.go(0)
      }
    },
    irrigation (time, msg) {
      var irrigationTime
      if (time !== '') {
        irrigationTime = time
      } else if (msg !== '') {
        irrigationTime = msg
      }
      console.log(irrigationTime)
      var obj = this
      axios({
        method: 'post',
        url: obj.$hostname + '/fieldcropirrigations/extraschedule',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          fieldCropId: obj.fc.fieldCropId,
          status: 1,
          manual: 1,
          durationMins: irrigationTime
        }
      }).then(function (res) {
        obj.customDuration = ''
        obj.irrigationDuration = ''
        obj.endTime = false
        obj.alert = true
        obj.onPageLoad()
      })
    },
    clickNo () {
      this.onPageLoad()
      this.dialog = false
    },
    closeAll () {
      if (this.msg === 'direct actuation') {
        this.changeActuatorStatus(0)
      } else if (this.msg === 'irrigation control') {
        this.changeIrrigationStatus(0)
      }
    },
    onPageLoad () {
      var obj = this
      clearInterval(obj.timex)
      obj.showCountDown = null
      obj.fieldcrops = []
      obj.fcDisableInfo = []
      obj.fcis = []
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/fieldactuator/all/?farmId=' + localStorage.getItem('farm'),
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          console.log(response)
          obj.fieldcrops = response.data.fieldcrop
          obj.fcDisableInfo = response.data.fieldirrigation
          obj.fcis = response.data.timer
          // for (var t = 0; t < obj.timerField.length; t++) {
          // console.log(obj.timerField[t].durationMins)
          //   obj.timer(obj.timerField[t].durationMins)
          // }
          var i
          var j
          var k
          var l
          var valveStatus = false
          // var timer = false
          for (i = 0; i < obj.fcDisableInfo.length; i++) {
            for (j = 0; j < obj.fieldcrops.length; j++) {
              if (obj.fieldcrops[j].fieldCropId === obj.fcDisableInfo[i].fieldCropId) {
                if (obj.fcDisableInfo[i].status === '0') {
                  obj.fieldcrops[j].irrigationStatus = 0 // obj.fcDisableInfo[i].status
                  break
                }
              }
            }
          }
          for (k = 0; k < obj.fieldcrops.length; k++) {
            valveStatus = false
            for (l = 0; l < obj.fieldcrops[k].field.fieldactuator.length; l++) {
              // console.log(obj.fieldcrops[k].field.fieldactuator[l].actuator.actuatorTypeId)
              if (obj.fieldcrops[k].field.fieldactuator[l].actuator.actuatorTypeId === 1) {
                if (obj.fieldcrops[k].field.fieldactuator[l].actuator.status === true) {
                  valveStatus = true
                  // console.log('ok')
                }
              }
            }
            obj.fieldcrops[k].valveStatus = valveStatus
          }
          for (i = 0; i < obj.fcis.length; i++) {
            for (j = 0; j < obj.fieldcrops.length; j++) {
              if (obj.fieldcrops[j].fieldCropId === obj.fcis[i].fieldCropId) {
                if (obj.fcis[i].status === '1' || obj.fcis[i].status === '2' || obj.fcis[i].status === '3') {
                  obj.fieldcrops[j].timer = true // obj.fcDisableInfo[i].status
                  obj.fieldcrops[j].timerDuration = obj.fcis[i].durationMins
                  // obj.timer(obj.fcis[i].durationMins)
                  obj.fieldcrops[j].timerStartTime = obj.fcis[i].startDateTime
                  obj.fieldcrops[j].timerData = obj.fcis[i]
                  var countDownDate = new Date(obj.fieldcrops[j].timerStartTime)
                  countDownDate.setMinutes(countDownDate.getMinutes() + obj.fieldcrops[j].timerDuration)
                  obj.fieldcrops[j].timerEndTime = countDownDate
                  // obj.timer2(obj.fieldcrops[j].timerDuration, obj.fieldcrops[j].timerStartTime, j)
                  break
                }
              }
            }
          }
          localForage.setItem('fieldcrop', obj.fieldcrops)
        }).catch(error => { console.log(error) })
      }).catch(function (err) {
        console.log(err)
      })
    },
    getStatus () {
      this.phaseStatus = JSON.parse(localStorage.getItem('phasedetail'))
      if (this.phaseStatus[0].status === 'true' && this.phaseStatus[1].status === 'true' && this.phaseStatus[2].status === 'true') {
        this.phaseStatus = 1
      } else {
        this.phaseInfoDailog = true
        this.phaseStatus = 0
      }
    },
    phaseInfo () {
      var obj = this
      obj.getStatus()
      obj.progress = false
    }
  },
  created () {
    this.progress = true
    this.onPageLoad()
  },
  mounted () {
    setInterval(this.phaseInfo, 3000)
  }
}
</script>
<style scoped>
</style>



