<template>
	<div>
		<v-container>
		  <v-layout justify-center>
		    <v-flex xs12 sm8>
		       <v-flex xs12>
		          <h3 justify-text-center>Update Password</h3>
		        </v-flex>
		        <v-form ref="form" @submit.prevent="onSave" v-model='valid'>
		        	<v-layout row wrap>
		           <v-alert  v-model="alert" :type="colorValue" dismissible>
		             {{message}}
		           </v-alert>
		             <v-flex xs12 md12 sm10 mt-4>
		               <v-text-field
		                      name="password"
		                      label="Current Password"
		                      type="password"
		                      v-model="userPass.password"
		                      :rules="passwordRules"
		                ></v-text-field>
              			</v-flex>
		              <v-flex xs12 md12 sm10>
		                <v-text-field
		                      name="newpass"
		                      label="New Password"
		                      type="password"
		                      v-model="userPass.newpass"
		                      :rules="passwordRules"
		                ></v-text-field>
		              </v-flex>
		              <v-flex xs12 md12 sm10>
		                <v-text-field
		                      name="confirmpass"
		                      label="Confirm Password"
		                      id="confirmpass"
		                      type="password"
		                      v-model="userPass.confirm"
		                      :rules="confPasswordRules"
		                ></v-text-field>
		              	</v-flex>
		            	</v-layout>
		       		 </v-form>
		            <v-btn  @click="backButton()">Back</v-btn>
		         <v-btn  @click="onSave()" :disabled='!valid'>Confirm</v-btn>
		      </v-flex>
		    </v-layout>
		</v-container>
	</div>
</template>
<script>
import navbar from '@/components/useful/NavBar.vue'
import localForage from 'localforage'
import axios from 'axios'
export default {
  components: {
    'nav-bar': navbar
  },
  data () {
    return {
      colorValue: 'success',
      updateText: '',
      message: '',
      alert: false,
      devise_id: '',
      token: '',
      email: '',
      valid: false,
      userPass: {
        password: '',
        newpass: '',
        confirm: ''
      },
      passwordRules: [
        v => !!v || 'Password is required',
        v => (v && v.length >= 8) || 'Password must be 8 characters'
      ],
      confPasswordRules: [
        v => !!v || 'Password is required',
        v => (v && v.length >= 8) || 'Password must be 8 characters',
        v => (v && this.userPass.newpass === this.userPass.confirm) || 'Passwords Does not Match'
      ]
    }
  },
  methods: {
    backButton () {
      this.$router.push('/farm/profile')
    },
    onSave () {
      console.log(this.userPass.password)
      var obj = this
      var userPass = obj.userPass
      console.log(this.token)
      console.log(this.devise_id)
      console.log(this.userEmail)
      axios({
        url: obj.$hostname + '/users/update_password',
        method: 'post',
        headers: {
          token: this.token,
          deviseid: this.devise_id,
          email: this.userEmail
        },
        data: {
          'password': userPass.password,
          'newpassword': userPass.newpass
        }
      }).then(response => {
        console.log(response.data)
        if (response.data.success === true) {
          this.alert = true
          obj.$refs.form.reset()
          obj.colorValue = 'success'
          obj.message = response.data.message
        } else {
          this.alert = true
          obj.$refs.form.reset()
          obj.colorValue = 'error'
          obj.message = response.data.message
        }
      }).catch(error => {
        console.log(error)
        this.alert = true
        obj.$refs.form.reset()
        obj.coloValue = 'error'
        obj.message = error.data.message
      })
    },
    fethcLocalForageData () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        console.log(value)
        obj.devise_id = value.devise_id
        obj.token = value.token
        obj.userEmail = value.email
      }).catch(error => {
        console.log(error)
      })
    },
    getData () {
      var obj = this
      obj.profile = []
      localForage.getItem('user').then(function (value) {
        obj.profile.push(value)
        console.log(value)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.fethcLocalForageData()
  }
}
</script>



